import React from "react";
import { Context } from "../../state/Provider";
import Student from "./Student/Student";
import "./Students.css";

const Students = () => {
  const {
    state: { students, searchName, searchTag },
  } = React.useContext(Context);

  const filteredStudents = getFilteredStudents({
    searchName,
    searchTag,
    students,
  });

  return (
    <section className="container">
      {filteredStudents.map((student, index) => {
        return (
          <Student
            key={index}
            pic={student.pic}
            fullName={student.fullName}
            email={student.email}
            company={student.company}
            skill={student.skill}
            grades={student.grades}
            id={student.id}
            tags={student.tags}
          />
        );
      })}
    </section>
  );
};

const getFilteredStudents = ({ searchName, searchTag, students }) => {
  const filteredStudentsByName = filterStudentsByName(students, searchName);

  const filteredStudentsByTag = filterStudentsByTag(
    filteredStudentsByName,
    searchTag
  );

  return filteredStudentsByTag;
};

const filterStudentsByName = (students, searchName) => {
  return searchName
    ? students.filter(({ fullName }) => {
        return fullName.toLowerCase().includes(searchName.toLowerCase());
      })
    : students;
};

const filterStudentsByTag = (filteredStudentsByName, searchTag) => {
  return searchTag
    ? filteredStudentsByName.filter(({ tags }) => {
        return tags
          ? tags.some((tag) =>
              tag.toLowerCase().includes(searchTag.toLowerCase())
            )
          : false;
      })
    : filteredStudentsByName;
};

export default Students;
