import * as React from "react";
import "./Student.css";
import Tags from "./Tags/Tags";

const Student = ({
  pic,
  fullName,
  email,
  company,
  skill,
  grades,
  id,
  tags,
}) => {
  const average =
    grades.map(Number).reduce((acc, init) => (acc = acc + init), 0) /
    grades.length;

  const testGrades = grades.map((grade, index) => {
    return (
      <li data-testid={`grade-${id}`} key={index} className="test-container">
        <span className="testNumber">{`Test ${index + 1}:`}</span>
        <span className="testValue">{`${grade}%`}</span>
      </li>
    );
  });

  const [isHidden, setHidden] = React.useState("false");

  const toggleClass = () => {
    setHidden(!isHidden);
  };

  const handleClick = (e) => {
    toggleClass(e);
  };

  return (
    <main data-testid={`student-${id}`} className="card">
      <section className="image-container">
        <div className="circle">
          <img src={pic} alt="student" />
        </div>
      </section>
      <section className="student-container">
        <header className="fullname">{fullName}</header>
        <article className="profile">
          <p>Email: {email}</p>
          <p>Company: {company}</p>
          <p>Skill: {skill}</p>
          <p data-testid={`average-${id}`}>Average: {average}%</p>
        </article>
        <aside
          data-testid={`grade-list-${id}`}
          className={isHidden ? "test-container-plus" : "test-container-minus"}
        >
          <ul>{testGrades}</ul>
        </aside>
        <Tags id={id} tags={tags} />
      </section>
      <section className="button-container">
        <button
          data-testid={`button-${id}`}
          id={id}
          onClick={handleClick}
          className="btn"
        >
          <i
            id={id}
            className={isHidden ? "fa fa-plus" : "fa fa-minus"}
            aria-hidden="true"
          ></i>
        </button>
      </section>
    </main>
  );
};

export default Student;
