import axios from "axios";
import * as React from "react";
import {
  addTag,
  fetchInitial,
  reducer,
  searchName,
  searchTag,
} from "./Reducer.js";

export const Context = React.createContext();

const URL = "https://api.hatchways.io/assessment/students";
const MAX_TAG_LENGTH = 20;
const ENTER_KEY = "Enter";

const initialState = {
  students: [],
  searchName: "",
  searchTag: "",
};

const fetchData = () => {
  return axios
    .get(URL)
    .then(({ data }) => {
      return data.students;
    })
    .catch(console.error);
};

const Provider = (props) => {
  const [state, dispatch] = React.useReducer(reducer, initialState);

  React.useEffect(() => {
    fetchData().then((students) => {
      dispatch(fetchInitial(students));
    });
  }, []);

  const onNameSearch = ({ target: { value } }) => dispatch(searchName(value));
  const onTagSearch = ({ target: { value } }) => dispatch(searchTag(value));
  const onTagAdd = ({ target, key }, id) => {
    const { value: tag } = target;

    if (key === ENTER_KEY && tag !== "" && tag.length <= MAX_TAG_LENGTH) {
      dispatch(
        addTag({
          id,
          tag,
        })
      );
      target.value = "";
    }
  };

  return (
    <Context.Provider
      value={{ state, dispatch, onNameSearch, onTagSearch, onTagAdd }}
    >
      {props.children}
    </Context.Provider>
  );
};

export default Provider;
