import React from "react";
import { Context } from "../../../../state/Provider";
import "./Tags.css";

// displayed tags
// adding a tag

const Tags = ({ id: studentId, tags }) => {
  const { onTagAdd } = React.useContext(Context);

  return (
    <aside className="tag-container">
      <ul className="unordered">
        {tags?.map((tag, index) => (
          <li
            data-testid={`tag-item-${studentId}`}
            key={index}
            className="list"
          >
            <span>{tag}</span>
          </li>
        ))}
      </ul>
      <input
        data-testid={`add-tag-field-${studentId}`}
        onKeyUp={(e) => onTagAdd(e, studentId)}
        className="tagInput"
        type="text"
        placeholder="Add a tag"
      />
    </aside>
  );
};

export default Tags;
