export const STUDENT_ACTIONS = {
  INITIAL: "INITIAL",
  SEARCH_NAME: "STUDENT_SEARCH_NAME",
  SEARCH_TAG: "STUDENT_SEARCH_TAG",
  ADD_TAG: "STUDENT_ADD_TAG",
};

export const fetchInitial = (data) => ({
  type: STUDENT_ACTIONS.INITIAL,
  data,
});

export const searchName = (data) => ({
  type: STUDENT_ACTIONS.SEARCH_NAME,
  data,
});

export const searchTag = (data) => ({
  type: STUDENT_ACTIONS.SEARCH_TAG,
  data,
});

export const addTag = (data) => {
  if (!data) return;

  return {
    type: STUDENT_ACTIONS.ADD_TAG,
    data,
  };
};

export const reducer = (state, action) => {
  switch (action.type) {
    case STUDENT_ACTIONS.INITIAL:
      return onInitialLoad(action);
    case STUDENT_ACTIONS.SEARCH_NAME:
      return onSearchName(state, action);
    case STUDENT_ACTIONS.SEARCH_TAG:
      return onSearchTag(state, action);
    case STUDENT_ACTIONS.ADD_TAG:
      return onAddTag(state, action);
    default:
      return state;
  }
};

export const onAddTag = (state, action) => {
  const studentsToUpdate = [...state.students];
  const { id, tag } = action.data;
  const index = id - 1;
  const currentStudent = studentsToUpdate[index];
  studentsToUpdate[index] = {
    ...currentStudent,
    tags: currentStudent.tags.concat(tag),
  };

  return {
    ...state,
    students: studentsToUpdate,
  };
};

const onInitialLoad = (action) => {
  return {
    students: action.data.map((student) => {
      return {
        ...student,
        fullName: `${student.firstName} ${student.lastName}`,
        tags: [],
      };
    }),
    searchName: "",
    searchTag: "",
  };
};

const onSearchTag = (state, action) => {
  return {
    ...state,
    searchTag: action.data,
  };
};

const onSearchName = (state, action) => {
  return {
    ...state,
    searchName: action.data,
  };
};
